                <div class="category-list col-11 col-md-3 mb-5">
                    <a href="#" @click.prevent="getAll()" class="list-group-item mb-2">ÊâÄÊúâÂïÜÂìÅ
                        <span class="badge rounded-pill"> {{ this.products.length }} </span>
                    </a>
                    <a href="#" @click.prevent="getCategory('Ë®ÇË£ΩËõãÁ≥ï')" class="list-group-item mb-2">Ë®ÇË£ΩËõãÁ≥ï
                        <span class="badge rounded-pill text-end"> {{ this.catNum.cake }} </span>
                    </a>
                    <a href="#" @click.prevent="getCategory('ÊùØÂ≠êËõãÁ≥ï')" class="list-group-item mb-2">ÊùØÂ≠êËõãÁ≥ï üçµ
                        <span class="badge rounded-pill text-end"> {{ this.catNum.cupcake }} </span>
                    </a>
                    <a href="#" @click.prevent="getCategory('Á≤æÁ∑ªÂ∞èÈªû')" class="list-group-item mb-2">Á≤æÁ∑ªÂ∞èÈªû
                        <span class="badge rounded-pill text-end"> {{ this.catNum.tiny }} </span>
                    </a>
                    <a href="#" @click.prevent="getCategory('È¶¨Âç°Èæç')" class="list-group-item mb-2">Á∂ìÂÖ∏È¶¨Âç°Èæç
                        <span class="badge rounded-pill text-end"> {{ this.catNum.macaron }} </span>
                    </a>
                    <a href="#" @click.prevent="getCategory('Á∂ìÂÖ∏‰∏ãÂçàËå∂')" class="list-group-item mb-2">‰∏ãÂçàËå∂ÂøÖÂÇô
                        <span class="badge rounded-pill text-end"> {{ this.catNum.tea }} </span>
                    </a>
                </div>
                <div class="col-11 col-md-8">
                    <div class="row justify-content-start">
                        <div class="col-12 col-md-6 col-lg-4 mb-4"
                            v-for= "item in filterCategory" 
                            :key="item.id">
                            <div class="products card">
                                <img :src="item.img" class="card-img-top" alt="item.title">
                                <div class="card-body text-start d-flex flex-column justify-content-center align-items-center">
                                    <h5 class="card-title">{{ item.title}}</h5>
                                    <div class="price-box d-flex justify-content-center align-items-center">
                                        <div><del>${{ item.price }} NTD</del></div>
                                        <div class="price fs-5 ms-3 text-red fw-bold">$9999 NTD</div>
                                    </div>
                                    <button class="cartbtn mt-2" type="button" @click="addToCart(item)">Âä†ÂÖ•Ë≥ºÁâ©Ëªä</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex';
export default {
    name:"Category",
    components: {
    },
    data() {
        return {
            filterCategory: [],
            catNum:{cake:0,cupcake:0,tiny:0,macaron:0,tea:0},
        }
    },
    methods: {
        getAll() {
            this.filterCategory = [ ...this.products ];
        },
        getCategory(itemCat) {
            return this.filterCategory = this.products.filter(item => item.category === itemCat);
        },
        getCatNum() {
            this.products.forEach((item) => {
                if(item.category === "Ë®ÇË£ΩËõãÁ≥ï"){
                    this.catNum.cake += 1 ;
                }
                if(item.category === "ÊùØÂ≠êËõãÁ≥ï"){
                    this.catNum.cupcake += 1 ;
                }
                if(item.category === "Á≤æÁ∑ªÂ∞èÈªû"){
                    this.catNum.tiny += 1 ;
                }
                if(item.category === "È¶¨Âç°Èæç"){
                    this.catNum.macaron += 1 ;
                }
                if(item.category === "Á∂ìÂÖ∏‰∏ãÂçàËå∂"){
                    this.catNum.tea= this.catNum.tea + 1 ;
                }
            })
        },
        addToCart(item) {
            this.$store.commit({
                type:"addToCart",
                id:item.id,
                name: item.name,
                quantity:1,
                img: item.img,
                price:900,
            });
        },
    },
    computed: {
        ...mapState({
            products:'products',
        }),
    },
    created() {
        this.getAll();  // Â∞ávuexÁöÑË≥áÊñô‰ª•spread syntaxÂÇ≥ÂÖ•
        this.getCatNum(); // Ë®àÁÆóÂêÑÂÄãÂàÜÈ°ûÁöÑÁ∏ΩÊï∏
    },
}
</script>
